body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
 word-wrap: break-word;

  background-color: rgb(137, 201, 238);
  font-family: Georgia, serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.voting{
  width: 250px;
  height: 250px;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.WalletInfo{
  width: 500px;
}

.Block{
  border:1px solid #fff;
  padding: 5%;
  margin: 2%;
}

.Transaction{
  padding: 5%;
}

.ConductTransaction, .TransactionPool {
  margin: 2%;
}

.wrapper{
display: grid;
grid-template-columns:repeat(auto-fit,
minmax(5px,5px));
gap: 2px;
justify-content: center;
}

.card {
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(5, 12, 104, 0.25);
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 200ms ease-in;
}

.card__title, .card__description{
  padding: 1px;
}

.card__btn{
  padding: 1px;
  font-weight: bold;
  font-size: small;
  color: aquamarine;
  border: azure;
  background: transparent;
  border-radius: 1%;
  transition: border 200ms ease-in, color 200ms ease-in;
}
